html {
  font-size: 100%;
  box-sizing: border-box;
}

*,
*::before,
*::after {
  box-sizing: inherit;
}

body {
  margin: 0;
  padding: 0;
  font-family: "Roboto", sans-serif;
}

.hero {
  display: flex;
  align-self: center;
  justify-content: center;
  align-items: center;
  margin-top: 6em;
  margin-left: auto;
  margin-right: auto;
}
@media (min-width: 64em) {
  .hero .container {
    display: flex;
  }
}
.hero__image {
  position: relative;
  width: 31.563em;
  height: 20.625em;
  border: solid 1px black;
}
.hero__text {
  display: flex;
  flex-direction: column;
  width: 23.75em;
  height: 15.625em;
  margin-left: 3.1875em;
}
.hero__text h1 {
  font-size: 2.625rem;
}
.hero__text span {
  font-size: 1rem;
}
.hero__text button {
  width: 8em;
  padding: 1em 2.5em 1em 2.5em;
  margin-top: 3.1875em;
  border-radius: 2em;
  border: none;
  background-color: hsl(20deg, 50%, 79%);
  color: hsl(0deg, 0%, 100%);
}